<template>
	<van-tag mark :type="tagType" size="large">{{billStateText}}</van-tag>
</template>

<script>
export default {
	name: 'stateTag',
	props: {
		billState: String
	},
	computed: {
		billStateText () {
			let stateText = ''
			switch (this.billState) {
			case '1' :
				stateText = '待确认'
				break
			case '2' :
				stateText = '待收货'
				break
			case '3' :
				stateText = '已完成'
				break
			}
			return stateText
		},
		tagType () {
			let tagType = ''
			switch (this.billState) {
			case '1' :
				tagType = 'danger'
				break
			case '2' :
				tagType = 'primary'
				break
			case '3' :
				tagType = 'success'
				break
			}
			return tagType
		}
	}
}
</script>
