<template>
	<div class="saleedi-order-list">
		<van-tabs v-model="active" color="#4c96f3" class="order-list-tab">
			<van-tab title="全部"></van-tab>
			<van-tab title="待确认"></van-tab>
			<van-tab title="待收货"></van-tab>
			<van-tab title="已完成"></van-tab>
		</van-tabs>
		<div class="order-list-warp">
			<van-list
				v-model="loading"
				:finished="finished"
				finished-text="没有更多了"
				@load="onLoad">
				<div
					v-for="(item, index) in orderListData"
					:key="index"
					class="order-list-item fx-edi-block">
					<div class="order-ent">{{item.organName}}</div>
					<orderBlock
						v-for="(billItem, billIndex) in item.bills"
						:orderData="billItem"
						:key="billIndex"
						@click="orderItemClick"></orderBlock>
				</div>
			</van-list>
		</div>
	</div>
</template>

<script>
import orderBlock from '@/components/OrderBlock.vue'
export default {
	name: 'orderList',
	components: {
		orderBlock
	},
	data () {
		return {
			active: '',
			orderListData: [],
			loading: false,
			finished: true
		}
	},
	created () {
		this.setTabActive(this.$route.query.billState)
	},
	watch: {
		active (val) {
			this.getQueryAllBill(this.title2State(val))
		}
	},
	methods: {
		setTabActive (billState) {
			switch (billState) {
			case '1' :
				this.active = 1
				break
			case '2' :
				this.active = 2
				break
			case '3' :
				this.active = 3
				break
			default :
				this.active = 0
				break
			}
		},
		onLoad () {
			console.log('加载更多数据')
		},
		title2State (index) {
			switch (index) {
			case 0 :
				return ''
			case 1 :
				return '1'
			case 2 :
				return '2'
			case 3 :
				return '3'
			}
		},
		getQueryAllBill (billState) {
			const params = {
				billState: billState
			}
			this.$fxHttp.requestPost({
				url: '/queryAllBill',
				data: params
			}).then(res => {
				this.orderListData = res
			})
		},
		orderItemClick (bill) {
			this.$router.push({
				path: '/orderDetails',
				query: {
					id: bill.id
				}
			})
		}
	}
}
</script>
